import { graphql } from 'gatsby';
import React from 'react';
import { Trans } from 'react-i18next';
import { Link, withIntl } from '../../i18n';
import PageWrapper from '../../components/PageWrapper';

const ArtistPage = ({ t, ...context }) => {
  const { key, name } = context.pageContext.page;

  return (
    <PageWrapper title={t('home.title')} {...context}>
      {name}
      {t(`artists:${key}.lastname`)}
    </PageWrapper>
  )
};

export const query = graphql`
  query ArtistQuery($path: String!) {
    sitePage(path: { eq: $path }) {
      context {
        locale
        page {
          key
          available
          name
          preposition
          lastname
          description
          birthdate
          deathdate
          works {
            key
            title
            images
          }
        }
      }
    }
  }
`;


export default withIntl()(ArtistPage);
